}

a,
blockquote,
body,
dd option,
div,
dl,
figure,
form,
h1,
h2,
h3,
h4,
html,
img,
input,
label,
li,
ol,
p,
section,
select,
span,
table,
tbody,
td,
tfoot,
th,
thead,
tr,
ul {
  border: 0;
  margin: 0;
  padding: 0;
}

ol,
