  background-color: var(--color-white);
  position: fixed;
  border-bottom: 1px solid var(--color-border);
  z-index: 2;
}

.main-wrapper {
  width: 100%;
  height: calc(100vh - 89px);
  background-color: var(--color-white);
  display: flex;
}

.center-div {
  width: 55%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 0rem 1rem;
}

.main-sec {
  padding-top: 5.5rem;
}

.custom-scroll::-webkit-scrollbar {
  width: 3px;
}

.custom-scroll::-webkit-scrollbar-track {
  background: var(--color-light-grey);
}

.custom-scroll::-webkit-scrollbar-thumb {
  background: var(--color-orange);
}

.custom-scroll::-webkit-scrollbar-thumb:hover {
  background: var(--color-orange);
}

.banner-wrapper {
  position: relative;
  background: url("../../images/banner.png") no-repeat;
  color: var(--color-light-grey);
  background-position: 60%;
  background-size: cover;
}
@media (min-width: 75em) {
  .banner-wrapper {
    background-position: 100%;
  }
}
.banner-innerwrap {
  min-height: 100vh;
}
.banner-content {
  max-width: 300px;
}
@media (min-width: 36em) {
  .banner-content {
    max-width: 410px;
  }
}
@media (min-width: 48em) {
  .banner-content {
    max-width: 500px;
  }
}
@media (min-width: 87.5em) {
  .banner-content {
    max-width: 640px;
  }
}
.banner-title {
  font-size: 1.9525rem;
  font-weight: var(--weight-700);
}
@media (min-width: 36em) {
  .banner-title {
    font-size: 2.5rem;
  }
}
@media (min-width: 48em) {
  .banner-title {
    font-size: 3.35rem;
  }
}
@media (min-width: 87.5em) {
  .banner-title {
    font-size: 4.125rem;
    line-height: 72px;
  }
}
.banner-para {
  font-size: 1.25rem;
  margin: 1.5rem 0 1.125rem;
}

.gallery-sec, .about-sec {
  padding: 4.0625rem 0.5rem;
}
@media (min-width: 87.5em) {
  .gallery-sec, .about-sec {
    padding: 8.125rem 0.5rem;
  }
}

.about-sec {
  background-color: var(--color-bg-gray);
}
.about-content {
  margin-bottom: 2.5rem;
}
.about-content > * + * {
  margin-top: 1.5rem;
}
@media (min-width: 62em) {
  .about-content {
    margin-bottom: initial;
  }
}
@media (min-width: 87.5em) {
  .about-content {
    padding-left: 1.875rem;
  }
  .about-content > * + * {
    margin-top: 1.875rem;
  }
}
.about-heading {
  max-width: 300px;
}
@media (min-width: 36em) {
  .about-heading {
    max-width: 400px;
  }
}
@media (min-width: 62em) {
  .about-heading {
    max-width: 100%;
  }
}
.about-para {
  margin-bottom: 1.25rem;
}
.about-para > * + * {
  margin-top: 1.25rem;
}
@media (min-width: 48em) {
  .about-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.gallery-sec {
  padding-left: 0;
  padding-right: 0;
  padding-bottom: 0;
}
.gallery-subpara {
  margin: 1.875rem 0 1.25rem;
  color: var(--color-light-grey-4);
}
.gallery-card {
  display: grid;
  max-width: 1920px;
  margin: 0 auto;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 48em) {
  .gallery-card {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 62em) {
  .gallery-card {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 87.5em) {
  .gallery-card {
    grid-template-columns: repeat(5, 1fr);
  }
}
.gallery-card--wrapper {
  display: flex;
}
.gallery-card--box {
  position: relative;
  overflow: hidden;
}
.gallery-card--box:hover .gallery-card--overlay {
  transform: translateY(0%) scale(1);
}
.gallery-card--overlay {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.125rem;
  text-align: center;
  position: absolute;
  inset: 0;
  background-color: hsl(var(--color-hsl-dark-1)/0.9);
  transform: translateY(-200%) scale(2);
  transition: transform 0.35s ease-in-out;
}
.gallery-card--label {
  font-size: 1.125rem;
}
@media (min-width: 48em) {
  .gallery-card--overlay {
    padding: 1.5rem;
  }
  .gallery-card--label {
    font-size: 1.5rem;
  }
}
@media (min-width: 75em) {
  .gallery-card--label {
    font-size: 1.875rem;
  }
}

.order {
  margin-top: 1.9375rem;
}
.order-card {
  padding: 1rem 1.5rem;
  border: 1px solid var(--color-light-grey-2);
}
.order-link:hover .order-card, .order-link :active .order-card {
  border: 1px solid var(--color-orange);
  transition: all ease-in-out 0.2s;
}
.order-number {
  font-size: 1rem;
  font-weight: var(--weight-700);
}
.order-time {
  font-size: 0.875rem;
  font-weight: var(--weight-500);
}